'use client';

import { create } from 'zustand';

type NavStoreProps = {
    title: string;
    setTitle: (title: string) => void;
    leftButton: {
        icon?: string;
        onClick?: () => void;
        render?: () => React.ReactNode;
    } | null;
    rightButton: {
        icon?: string;
        onClick?: () => void;
        render?: () => React.ReactNode;
    } | null;

}

export const useNavStore = create<NavStoreProps>((set) => ({
    title: '',
    setTitle: (title: string) => set({ title }),
    leftButton: null,
    rightButton: null,
}));